"use strict";

import Namespace from './Namespace';
import JSObjectNamespace from './JSObjectNamespace';
import {RookAttributeNotFound} from "../../exceptions";

export default class ContainerNamespace extends Namespace {

    constructor(dict=null) {
        super();

        if (dict === null) {
            this.namespaces = {};
        } else {
            this.namespaces = dict;
        }
    }

    callMethod(name, args) {
        switch (name) {
            case "size":
                return new JSObjectNamespace(Object.keys(this.namespaces).length);
            default:
                return super.callMethod(name, args);
        }
    }

    readAttribute(name) {
        if (this.namespaces[name] === undefined) {
            throw new RookAttributeNotFound(name);
        }
        return this.namespaces[name];
    }

    writeAttribute(name, value) {
        this.namespaces[name] = value;
    }

}
